<template>
  <dispatchers-page-wrapper>
    <dispatchers-list
      :is-visible="isVisibleList"
      :dispatchers-list="dispatchers"
    />
    <router-view />
  </dispatchers-page-wrapper>
</template>

<script>
import DispatchersPageWrapper from '@modules/dispatchers/components/DispatchersPageWrapper';
import DispatchersList from '@modules/dispatchers/components/DispatchersList';

import * as constants from '@modules/dispatchers/constants';
import { identity } from 'ramda';
import EventBus from '@utils/eventBus';

import { createNamespacedHelpers } from 'vuex';

const {
  mapActions,
  mapState,
  mapGetters,
  mapMutations,
} = createNamespacedHelpers('dispatchers');

export default {
  name: 'Dispatchers',
  components: {
    DispatchersPageWrapper,
    DispatchersList,
  },
  data: () => ({
    routeId: null,
    isEditRoute: false,
  }),
  computed: {
    isVisibleList() {
      return (
        this.$route.name === this.$constants.ROUTE_NAMES.DISPATCHERS
        || (this.$route.name === this.$constants.ROUTE_NAMES.DISPATCHER && !this.$vuetify.breakpoint.mobile)
      );
    },
    ...mapState({
      dispatchers: (state) => state.dispatchers.data,
    }),
    ...mapGetters({
      activeDispatcherId: constants.getters.ACTIVE_DISPATCHER_ID,
    }),
  },
  watch: {
    '$route.params.id': {
      immediate: true,
      handler(nextId) {
        this.routeId = nextId;
      },
    },
    '$route.fullPath': {
      immediate: true,
      handler(path) {
        this.isEditRoute = /edit/.test(path);
      },
    },
  },
  created() {
    this.getDispatchers()
      .catch(() => {
        this.$notificationError('Sorry, we could not get dispatchers. Please try again or contact administrator.');
      });
  },
  mounted() {
    EventBus.$on(this.$constants.EVENTS.REDIRECT_EVENT, this.redirectToExistDispatcher);
  },
  beforeUpdate() {
    if (!this.isEditRoute) {
      if (!this.routeId && !this.$vuetify.breakpoint.mobile) {
        let path = {
          name: this.$constants.ROUTE_NAMES.DISPATCHER,
          params: { id: this.activeDispatcherId },
        };
        if (!this.activeDispatcherId) {
          path = {
            name: this.$constants.ROUTE_NAMES.DISPATCHERS,
          };
        }
        this.$router.push(path).catch(identity);
      } else {
        this.$router.push(
          {
            name: this.$constants.ROUTE_NAMES.DISPATCHER,
            params: { id: this.routeId },
          },
        ).catch(identity);
        this.updateActiveDispatcherId(this.routeId);
      }

      if (!this.routeId && this.$vuetify.breakpoint.mobile) {
        this.$router.push(
          {
            name: this.$constants.ROUTE_NAMES.DISPATCHERS,
            params: { id: this.activeDispatcherId },
          },
        ).catch(identity);
      }
    }
  },
  methods: {
    ...mapActions({
      getDispatchers: constants.actions.GET_DISPATCHERS,
    }),
    ...mapMutations({
      updateActiveDispatcherId: constants.mutations.UPDATE_ACTIVE_DISPATCHER_ID,
    }),
    redirectToExistDispatcher() {
      this.$router.push(
        {
          name: this.$constants.ROUTE_NAMES.DISPATCHERS,
          params: { id: this.dispatchers[0].id },
        },
      ).catch(identity);
    },
  },
};
</script>
